<template>
	<nv-head page-title="购物车" menu-bar="true"></nv-head>	
	<div class="page-loading" id="freshen">加载中...</div>
	<div class="cart-list inner">
			<div class="cart-list-header">1</div>
			<div class="cart-list-body">23</div>
			<div class="cart-list-footer">
				<button class="button btn-empty btn-small"><i class="iconfont">&#xe607;</i></button>
			</div>
	</div>
	<div class="cart-list inner">
		asd
	</div>
	<div class="ui-list inner-border no-top">
		<div class="cell">
			<div class="cell-checkbox">
				<label for="checkbox-1">
					<input type="checkbox" id="checkbox-1" vlaue="c"  v-model="checkbox">
				</label>
			</div>
			<div class="cell-thumb"><img src="../assets/images/logo.png" alt=""></div>
			<div class="cell-bd">
				<h3>时尚高跟女鞋</h3>
				<div v-if="editStatus">
					<p>颜色：白色 尺码：36</p>
					<p><span class="price-color">￥321.00</span>  运费：<span class="gray">￥9</span></p>
				</div>
				<div v-else>
					<button type="button" @click="lessNum">-</button>
					<input type="text" name="" id="" value="1" v-model="count">
					<button type="button" @click="addNum">+</button>
				</div>
			</div>
			
		</div>
		<div class="cell">
			<div class="cell-checkbox">
				<input type="checkbox" id="checkbox-2" vlaue="b" v-model="checkbox">
			</div>
			<div class="cell-thumb"><img src="../assets/images/logo.png" alt=""></div>
			<div class="cell-bd">
				<h3>时尚高跟女鞋</h3>
				<p>颜色：白色 尺码：36</p>
				<p><span class="price-color">￥321.00</span>  运费：<span class="gray">￥9</span></p>
			</div>
			
		</div>
		<div class="cell">
			<div class="cell-checkbox">
				<input type="checkbox" id="checkbox-3" vlaue="a" v-model="checkbox">
			</div>
			<div class="cell-thumb"><img src="../assets/images/logo.png" alt=""></div>
			<div class="cell-bd">
				<h3>时尚高跟女鞋</h3>
				<p>颜色：白色 尺码：36</p>
				<p><span class="price-color">￥321.00</span>  运费：<span class="gray">￥9</span></p>
			</div>
			
		</div>
		
	</div>
	<div class="bot-bar">
		<div class="pull-left">
			
			<label for="all">
				<input type="checkbox" id="all" name="Mike" v-model="checkedAll">全选</label>
			<span v-if="editStatus">合计： 12323</span>
		</div>
		<div class="pull-right" v-if="editStatus">结算(2)</div>
		<div class="pull-right" v-else @click="delGoods">删除</div>
	</div>
	<nv-alert :content="alert.txt" :show="alert.show" :class="alert.type"></nv-alert>
</template>

<script>
	export default {
		data (){
			let self = this;
			return {
				times: null,
				checkedAll: false,
				editStatus: true,
				checkbox: [],
				alert: {
                    txt: '',
                    show: false,
                    type: '',
                    hideFn:function(times){
                        let timer = times ? times: 2000;
                        setTimeout(function () {
                            self.alert.show = false;
                        }, timer);
                    }
                }
			}
		},
		ready (){

			let nub = 1;
			let self = this;
			self.checkbox = false;
			document.getElementById('freshen').classList.add('hide');
			document.getElementById('all').addEventListener('click',function(){
				
				if(self.checkedAll){
					self.checkedAll = false;
					self.checkbox = false;
				}else{
					self.checkedAll = true;
					self.checkbox = true;
				}
	    			
	    		    console.log(self.checkbox);
	    	},true);
		},
	    methods: {

	    	delGoods (){
	    		let self = this;
	    		if(!self.checkedAll){
	    			self.alert.txt = '请选择所要删除的商品！';
	    			self.alert.type = 'alert';
		    		self.alert.show = true;
		    		self.alert.hideFn();
	    		}else{
	    			self.alert.txt = '删除成功！';
	    			self.alert.type = 'seccess';
		    		self.alert.show = true;
		    		self.alert.hideFn();
	    		}
	    		return;
	    	},
	    	addNum (){
	    		let self = this;
	    		self.count++;
	    		self.ajax();
	    		
	    	},
	    	lessNum (){
	    		let self = this;
	    		self.count--
	    		self.ajax();
	    	},
	    	ajax (){
	    		let self = this;
	    		clearTimeout(self.times);  //防止快速点击重复请求，间隔200毫秒，以最后一次请求为准
	    		self.times = setTimeout(function(){
	    			console.log('发起请求~');
	    		},350);
	    	}
	    },
	    components: {
			"nvHead":require('../components/header.vue'),
			"nvAlert":require('../components/alert.vue')
		}
	}
</script>


