export default function(router){
	router.map({
		'/':{
			name: 'home',
			component:require('./views/App.vue')
		},
		/* 404路由 */
        '*': {
            component: require('./views/App.vue')
        },
		'/list':{
			name: 'list',
			component:require('./views/list.vue')
		},
		'/detail/:id':{
			name: 'detail',
			component:require('./views/detail.vue')
		},
		'/cart':{
			name: 'cart',
			component:require('./views/cart.vue')
		},
		'/search':{
			name: 'search',
			component:require('./views/search.vue')
		},
		'/cuxiao':{
			name: 'cuxiao',
			component:require('./views/cuxiao.vue')
		},
		'/ucenter':{
			name: 'ucenter',
			component:require('./views/ucenter.vue'),
			auth: true
		},
		'/login':{
			name: 'login',
			component:require('./views/login.vue')
		},
		'/ucenter/setting':{
			name: 'setting',
			component:require('./views/setting.vue')
		},
		'/ucenter/address':{
			name: 'address',
			component:require('./views/address.vue')
		},
		'/ucenter/collect':{
			name: 'collect',
			component:require('./views/collect.vue')
		},
		'/ucenter/order':{
			name: 'order',
			component:require('./views/order.vue')
		},
		'/ucenter/balance':{
			name: 'balance',
			component:require('./views/balance.vue')
		}
	})
}


